    margin: 0;
  }

  .container-fluid {
    padding-right: 0;
    padding-left: 0;
  }
}


.content-page {
  flex-direction: column;
  justify-content: center;
}

.header-bg {
  background-color:white;
  border-radius: 5px;
  padding: 0 15px;
}

.border-right {
  border-right: 1px solid black !important;
}

.border-left {
  border-left: 1px solid black !important;
}

.padding-right {
  padding-right: 20px;
}

.padding-left {
  padding-left: 20px;
}

.text-right {
  text-align: right;
}

