/* NAVBAR STYLES */
.amk {
  height: 53px;
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 1px;
  padding-bottom: 1px;
  padding-left: 1px;
}
@media (max-width: 960px){
  .amk {
    /* margin-right: 5px; */
    /* margin-top: 5px; */
  }
  .amk-circle {
    fill: #ffffff;
  }
}
@media (min-width: 960px){
  .amk-text{
    fill: #ffffff;
  }
  .amk-rect {
    fill: #ffffff;
  }
  .amk-circle {
    stroke: #ffffff;
  }
}
.navbar-light .navbar-nav .nav-link {
  color:white;
}

@media (max-width: 960px) {
  .navbar-light .navbar-nav .nav-link {
    color:steelblue;
  }
}

.navbar-nav {
  display: inline-flex;
}


.navbar-light .navbar-nav .nav-link:hover {
  color: black;
  /* background-color: white; */
}

.navbar-nav .nav-link {
  padding: 1px 1rem;
}
/* .nav-link:hover {
  text-decoration: underline;
} */
.navbar {
  background-color: steelblue;
  /* margin: 0 65px; */
  /* border-bottom: 2px solid black; */
}

.nav-header {
  margin-bottom: 0;
  display: inline-block;
  padding-left: 0;
}

.navbar-links {
  padding-top: 0;
  padding-bottom: 3px;
}

.hide-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 0;
  cursor: pointer;
}

.show-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  cursor: pointer;
}

.nav-container {
  z-index: 100;
}

.nav-item {
  text-align: right;
}

.current {
  text-decoration: underline;
  /* color: mediumvioletred; */
  color: #f44336;
  color: #ff5851;
  color: crimson;
  color: steelblue;
}

.navbar-light .navbar-toggler {
  border: none;
}
.nav-container {
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 960px) {
  .navbar-collapse {
    margin-top: 35px;
  }
}
.navbar-toggler {
  position: absolute;
  top: 0;
  right: 0;
}

/* ABOUT ME STYLES */
.about-flex-collapse {
  display: flex;
}
@media (max-width: 960px) {
  .about-flex-collapse {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.about-me-container {
  display: flex;
  align-content: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.about-me-paragraph {
  width: 300px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: right;
}

.me {
  /* animation: App-logo-spin infinite 20s linear; */
  /* height: 40vmin; */
  /* pointer-events: none; */
  display: block;
  max-width: 60vmin;
  max-height: 40vmin;
  width: auto;
  height: auto;
}

/* RESUME STYLES */
.resume-container {
  display: flex;
  flex-direction: column;
}

.experience-container {
  display: flex;
  justify-content: flex-end;
}

.education-header {
  padding-right: 20px;
}

.experience-header {
  padding-left: 20px;
}

.education-content {
  padding-left: 20px;
}

.experience-content {
  padding-right: 20px;
}

.education-container {
  display: flex;
  justify-content: flex-start;
}

@media (max-width: 960px) {
  .navbar {
    background-color: transparent;
    padding: 0;
    border-bottom: 0;
    margin: 0;
  }
  .nav-link {
    background-color: white;
    padding-left: 0;
  }
  .resume-break {
    display: none;
  }
  /* Experience */
  .experience-container {
    flex-direction: column-reverse;
    align-items: flex-end;
  }

  .experience-container .border-right {
    border-right: 0 !important;
  }

  .experience-container .padding-left {
    padding-left: 0;
  }

  .experience-container .padding-right {
    padding-right: 0;
  }

  .experience-container p {
    border-top: 1px solid black;
    padding-top: 10px;
  }

  .experience-header {
    border-bottom: 1px solid black;
    font-size: 30px;
    padding-left: 0px
    /* padding-bottom: 5px; */
  }

  .experience-content {
    padding-right: 0px;
  }

  /* Education */
  .education-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .education-header {
    border-bottom: 1px solid black;
    font-size: 30px;
    padding-right: 0px
    /* padding-bottom: 5px; */
  }

  .education-content {
    padding-left: 0px;
  }

  .education-container .border-left {
    border-left: 0 !important;
  }

  .education-container .padding-left {
    padding-right: 0;
  }

  .education-container .padding-right {
    padding-left: 0;
  }
}

.resume-text {
  text-align: right;
  max-width: 30vw;
}

.resume-divider {
  width: 1px;
  background-color: black;
  margin: 0 20px;
}

.experience-header {
  font-size: 30px;
}

