/* PROJECTS STYLES */

.projects-page {
  align-items: flex-start;
  padding-top: 20px;
  padding-bottom: 20px;
}

.projects-title {
  color: steelblue;
}

.projects-header-col {
  height: 100%;
  border-right: 1px solid black;
}

@media (max-width: 768px) {
  .projects-header-col {
    height: auto;
    border-right: 0;
    /* border-bottom: 1px solid black; */
  }
  .projects-title {
    border-bottom: 1px solid black;
  }
  .projects-page {
    display: flex;
    flex-direction: column;
  }
  .project-container {
    padding-top: 10px;
  }
}

.project-header {
  color: black;
  font-size: 30px;
}

.project-icon {
  width:18px;
  margin-left: 8px;
  margin-bottom: 3px;
}

.project-link:hover {
  color: steelblue;
  text-decoration: none;
}

.project-icon:hover {
  fill: steelblue;
}

/* FOOTER STYLES */


.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  background-color: white;
  text-align: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.link-icon {
  width: 30px;
  margin: 0 4px 8px 4px;
}

.link-icon:hover {
  fill: steelblue;
}

.linkedin {
  margin-left: 8px;
}

.line {
  background-color: black;
  display: inline-block;
  height: 1px;
  width: 100%;
  margin-left: 4px;
  margin-right: 8px;
}